import React from 'react';
import { useHistory } from 'react-router-dom';
import Footer from '../footer';
import Header from '../header';

export default function Landing() {
  const history = useHistory();
  const handleStarted = () => {
    history.push('intro');
  };

  return (
    <div style={{
      backgroundImage: `url('${process.env.PUBLIC_URL}/background.jpg')`,
      backgroundSize: '100%',
      width: '100%',
      height: '100%',
      backgroundRepeat: 'no-repeat',
      backgrounddivor: '#910100'
    }}>
      <Header />
      <div className='content'>
        <div className='head'>
          <div>
          <a href='https://www.youtube.com/channel/UC8sO6l27VGFjXRX1gaw7q3g' target="_blank">
            Santuy No Life Youtube Channel
          </a>
          </div>
          <div style={{fontFamily: 'paladins'}}>
            Gaming Esport
          </div>
        </div>
        <div className='text'>
          <p>
            Santuy No Life  (SNL) adalah team Esport Gaming yang beretika baik dan  ramah, team ini juga sudah sering melalukan  banyak Tournament online, yaitu game, Valorant, Rainbowsix, Dota2, Point blank dll. SNL juga pernah menyelenggarakan event Tournament Valorant Desember 2021 silam, SNL juga mempunyai channel Youtube, dan di chennel tersebut sudah banyak sekali konten dan streaming yang crew SNL upload, Tekan Starting untuk melihat Opening Chennel Santuy No Life.
          </p>
          <div style={{display: 'flex'}}>
            <div className='get-started' onClick={handleStarted}>Started</div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  )
}
